import uniPopup from "@/components/uni-popup/uni-popup.vue" //弹窗选择工程师
import anUploadImg from '@/components/an-uploadImg/an-uploadImgmaintenance.vue' //上传图片
export default {
	components: {
		uniPopup,
		anUploadImg
	},
	data() {
		return {
			state: 0, //    1 指派     全场的状态 ，
			url: "", //    判断后进行修改请求路径
			data: {}, //    数据
			Time: '', //时间
			gcss: [], //获取来的工程师列表
			gcs: [], // 已选择的工程师id
			index: 0, //选择工程师的下标
			gcsName: '', // 选择的工程师的name集合
			EngineerDtos: [], //指派的工程师
			EquipmentDtos: [], //待保养的设备
			Base64: '',
			workcontent:'',//备注内容
		};
	},
	mounted() {
		uni.showLoading({
			title: '加载中'
		}); //声明过渡
		//获取当前时间
		var curDate = new Date()
		var curYear = curDate.getFullYear(); //获取完整的年份(4位,1970-????)
		var curMonth = curDate.getMonth() + 1; //获取当前月份(0-11,0代表1月)
		var curDay = curDate.getDate(); //获取当前日(1-31)
		this.Time = curYear + '-' + curMonth + '-' + curDay
		this.state = this.$store.state.Maintenancestatus //获得上级的状态
		console.log(this.state)
		if (this.state == 1) {
			this.url = 'Maintain/GetMaintainBaseInfo'
		} //指派
		if (this.state != 1) {
			this.url = 'Maintain/GetMaintainDetailInfo'
		} //处理   
		this.http()
		this.httpgcs()
	},
	methods: {
		//完成保养
		GoOK: function() {
			uni.request({
				url: this.$store.state.url + 'Maintain/CompleteMaintain',
				method: 'POST',
				data: {
					Id: this.$store.state.MaintenancestatusID,
					WorkContent : this.workcontent
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					var asd = JSON.parse(res.data).Code
					if (asd == 0) {
						uni.navigateTo({
							url: "../work.3",
						});
					} else {
						uni.showToast({
							title: JSON.parse(res.data).Msg || '错误',
							icon: "none"
						})
					}
				}
			});
		},
		//上传照片
		addimg: function(items, isd) {
			var that = this
			var timer = null;
			timer = setInterval(function() {
				if (that.$store.state.imgListXAIBIAO0 != '') {
					clearInterval(timer)
					console.log(that.$store.state.imgListXAIBIAO0)
					uni.request({
						url: that.$store.state.url + 'Maintain/UploadMaintainPhoto',
						method: 'POST',
						data: {
							Id: items.PhotoId,
							Type: isd,
							FileStr: that.$store.state.imgListXAIBIAO0
						},
						header: {
							'Token': that.$store.state.token
						},
						success: (res) => {
							var asd = JSON.parse(res.data).Code
							if (asd == 0) {
								console.log(JSON.parse(res.data))
							} else {
								uni.showToast({
									title: JSON.parse(res.data).Msg || '错误',
									icon: "none"
								})
							}
						}
					});
				}
			}, 500);
			that.$store.state.imgListXAIBIAO0 = ''
		},

		//添加照片组
		addimslists: function(id) {
			uni.showLoading({title: '正在添加'});
			var athat = this
			uni.request({
				url: this.$store.state.url + 'Maintain/AddEquPhoto',
				method: 'POST',
				data: {
					Id: id
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					var asd = JSON.parse(res.data).Code
					if (asd == 0) {
						   
						athat.http()
					} else {
						uni.showToast({
							title: JSON.parse(res.data).Msg || '错误',
							icon: "none"
						})
					}
				}
			});
		},

		//接受按钮
		Go: function(i) {
			uni.request({
				url: this.$store.state.url + 'Maintain/UpdateMaintainState',
				method: 'POST',
				data: {
					id: this.$store.state.MaintenancestatusID,
					State: i
					// 已指派2：接受3   已指派2：拒绝1
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					var asd = JSON.parse(res.data).Code
					if (asd == 0) {
						uni.navigateTo({
							url: "../work.3",
						});
					} else {
						uni.showToast({
							title: JSON.parse(res.data).Msg || '错误',
							icon: "none"
						})
					}
				}
			});
		},

		//初始接口   判断状态后调用
		http: function() {

			uni.request({
				url: this.$store.state.url + this.url,
				method: 'GET',
				data: {
					Id: this.$store.state.MaintenancestatusID
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					setTimeout(function() {
						uni.hideLoading()
					}, 500); //成功后取消过渡
					var code = JSON.parse(res.data).code || JSON.parse(res.data).Code
					if (code == 0) {
						setTimeout(function () {uni.hideLoading();}, 500);    
						var asd = JSON.parse(res.data).Data
						if (asd.Msg == 'Success') {
							this.data = asd.BaseInfo //详细信息
							this.EngineerDtos = asd.EngineerDtos
							this.EquipmentDtos = asd.EquipmentDtos
						} else {
							this.data = asd //基本信息
						}
						console.log(asd)
					} else {
						uni.showToast({
							title: "超时",
							icon: "none"
						})
					}
				}
			});
		},
		//获取gcs
		httpgcs: function() {
			uni.request({
				url: this.$store.state.url + 'System/GetEmployeeMini',
				method: 'GET',
				data: {
					type: '1'
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					var code = JSON.parse(res.data).code || JSON.parse(res.data).Code
					if (code == 0) {
						this.gcss = JSON.parse(res.data).Data
						for (var i = 0; i < this.gcss.length; i++) {
							this.gcss[i]['switch'] = false
						}

					} else {
						uni.showToast({
							title: "超时",
							icon: "none"
						})
					}
				}
			});
		},
		//时间
		DateChange(e) {
			this.Time = e.detail.value
		},
		//指派gcs
		zhi: function() {
			if (this.gcs[0]) {
				uni.request({
					url: this.$store.state.url + 'Maintain/AssignTaskForM',
					method: 'POST',
					data: {
						Id: this.$store.state.MaintenancestatusID,
						EIds: this.gcs,
						BeginTime: this.Time
					},
					header: {
						'Token': this.$store.state.token
					},
					success: (res) => {
						var asd = JSON.parse(res.data).Code
						if (asd == 0) {
							uni.navigateTo({
								url: "../work.3",
							});
						} else {
							uni.showToast({
								title: JSON.parse(res.data).Msg || '错误',
								icon: "none"
							})
						}
					}
				});
			} else {
				uni.showToast({
					title: "请选择工程师",
					icon: "none"
				})
			}

		},
		//点击工程师选择
		onKeyInputs: function(event) {
			// console.log(event.target.value)
			this.$refs.popup.open()
		},
		//确认gcs
		close: function() {
			this.gcs = []
			this.gcsName = ''
			this.$refs.popup.close()
			for (var i = 0; i < this.gcss.length; i++) {
				if (this.gcss[i].switch) {
					this.gcs.push(this.gcss[i].EmployeeId)
					this.gcsName += "/" + this.gcss[i].Name

				}
			}
			for (var i = 0; i < this.gcss.length; i++) {
				this.gcss[i].switch = false
			}
		},
		DataChange: function(e) {},
		switchs: function(i) {
			this.$set(this.gcss[i], 'switch', !this.gcss[i].switch)
		}

	}
}
